
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
components: {
XHeader,
ViewBox,
Swipeout,
SwipeoutItem,
SwipeoutButton,
Flexbox,
FlexboxItem
},
mounted() {
this.update();
},
methods: {
update() {

this.$post(
"address/getList", {},
res => {
console.log("地址的", res);
this.list = res.msg;
}
);

},

del(item, index, list) {
this.$post("address/del", {
address_id: item.address_id
},
res => {
console.log(res);
if(res.res >= 1) {
list.splice(index, 1);
this.$vux.toast.show({
text: "删除成功",
type: "text",
position: "middle"
});
}
if(res.res < 1) {
this.$vux.toast.show({
text: "删除失败",
type: "text",
position: "middle"
});
}
})
},
edit(item, index, list) {
var address_id = item.address_id;
var name = item.people;
var phone = item.phone;
var info = item.info;
var region = item.region;
this.$router.push({
path: "/addAddress/addAddress",
name: '/addAddress/addAddress',
params: {
openEdit: "1",
address_id: address_id,
name: name,
phone: phone,
info: info,
region: region
}
});
},
setDefault(item, index, list) {
this.$post("address/setDefault", {
address_id: item.address_id
},
res => {
console.log(res);
if(res.res >= 1) {
this.$vux.toast.show({
text: "设置成功",
type: "text",
position: "middle"
});
}
if(res.res < 1) {
this.$vux.toast.show({
text: "设置失败",
type: "text",
position: "middle"
});
}
})
},
handleEvents(type) {
console.log("event: ", type);
},
toAddAddress() {
this.$router.push({
path: "/addAddress/addAddress"
});
}
},
data() {
return {
list: []
};
}
};



